<template>
  <span>
    <template v-if="onlySymbol">
      {{ me.currency_symbol }}
    </template>
    <template v-else-if="me.currency_prefix">
      {{ me.currency_symbol }}{{ money(sanitizeValue, { currency: me.currency }).replace('-', '') }}
    </template>
    <template v-else-if="!me.currency_prefix">
      {{ `${money(sanitizeValue, { currency: me.currency }).replace('-', '')} ${me.currency_symbol}` }}
    </template>
  </span>
</template>

<script>
import { defineComponent, computed } from 'vue'
import { useUser } from 'composable/user'
import { money } from 'utils/number'

export default defineComponent({
  props: {
    value: [Number, String],
    onlySymbol: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const { me } = useUser()
    const sanitizeValue = computed(() => {
      if (!props.value || typeof props.value === 'number') {
        return Number(props.value) ?? 0
      }

      const valueTest = props?.value
      const contains = valueTest.split('')
      if (contains.includes(me.value.currency_symbol)) {
        return valueTest.replace(me.value?.currency_symbol, '')
      }

      return valueTest
    })
    return { me, money, sanitizeValue }
  }
})
</script>